<template>
    <div class="customer">
        <label class="checkout-title-text">Thng tin giao hng</label>
        <div class="pos-rel mt-16">
            <span class="label-sub">H v tn</span>
            <input 
                name="name" 
                placeholder="Nhp h v tn khch hng" 
                class="form-control" 
                v-model="formData.name"
            />
        </div>
        <div class="pos-rel mt-16">
            <span class="label-sub">S in thoi</span>
            <input 
                name="phone" 
                placeholder="Nhp s in thoi" 
                class="form-control" 
                v-model="formData.phone"
            />
        </div>
        <div class="pos-rel mt-16">
            <span class="label-sub">a ch</span>
            <input 
                name="address" 
                placeholder="Nhp a ch" 
                class="form-control" 
                v-model="formData.address"
            />
        </div>
        <div class="pos-rel mt-16">
            <span class="label-sub">Ghi ch</span>
            <textarea 
                name="note" 
                placeholder="Nhp ni dung ghi ch" 
                class="form-control"
                v-model="formData.note"
            ></textarea>
        </div>
    </div>
</template>


<script>
export default {
    data() { 
        return {
            formData: {
                name: '',
                phone: '',
                address: '',
                note: '',
            }
        }
    },
    created() {
        this.setupData()
    },
    watch: {
        formData: {
            handler(newCarts) {
                this.$emit('getDataShipping', this.formData);
            },
            deep: true
        }
       
    },
    methods: {
        setupData() {
            const shipping = JSON.parse(localStorage.getItem('shipping'));
            if (Object.keys(shipping)) {
                this.formData = {
                    name: shipping.name,
                    phone: shipping.phone,
                    address: shipping.address,
                    note: shipping.note,
                }
            }
        }
    }
};
</script>